.customerContainer {
  display: -ms-flexbox;
  display: flex;
  height: 175px;
  margin-top: -10px;
}

.avatarContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row
}

.avatar {
  display: inline-block;
  width: 200px;
  height: 150px;
  -webkit-animation: onEnter .4s linear;
          animation: onEnter .4s linear;
}

.nudge {
  -webkit-animation: nudge .4s linear;
          animation: nudge .4s linear;
}

.angry {
  -webkit-animation: angry .4s infinite;
          animation: angry .4s infinite;
}

@-webkit-keyframes nudge {
  
  0% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  
  33% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
  
  66% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
}

@keyframes nudge {
  
  0% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  
  33% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
  
  66% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
}

@-webkit-keyframes onEnter {
  
  0% {
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg);
  }
  
  33% {
    -webkit-transform: rotate(7deg);
            transform: rotate(7deg);
  }
  
  66% {
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg);
  }
}

@keyframes onEnter {
  
  0% {
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg);
  }
  
  33% {
    -webkit-transform: rotate(7deg);
            transform: rotate(7deg);
  }
  
  66% {
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg);
  }
}

@-webkit-keyframes angry {
  
  0% {
    -webkit-transform: scale(1.01, 1.01);
            transform: scale(1.01, 1.01);
  }
  
  33% {
    -webkit-transform: scale(1.03, 1.03);
            transform: scale(1.03, 1.03);
  }
  
  66% {
    -webkit-transform: scale(1.05, 1.05);
            transform: scale(1.05, 1.05);
  }
}

@keyframes angry {
  
  0% {
    -webkit-transform: scale(1.01, 1.01);
            transform: scale(1.01, 1.01);
  }
  
  33% {
    -webkit-transform: scale(1.03, 1.03);
            transform: scale(1.03, 1.03);
  }
  
  66% {
    -webkit-transform: scale(1.05, 1.05);
            transform: scale(1.05, 1.05);
  }
}


.orderMenu {
  text-align: center;
  width: 80px;
  height: 24px;
  float: right;
}

.emptySeat {
  display: inline-block;
  width: 200px;
  height: 150px;
}

.customerOrderContainer {
  width: 200px;
}


